<template>
  <div class="appointment flex-column border-secondary rounded">
    <div class="p1 bg-secondary base">{{month}}</div>
    <div class="flex-row align-center">
      <div class="font4 p2 border-right-secondary">{{day}}</div>
      <div class="flex-one p1">{{time}}</div>

      <div class="flex-column accept-button m2-right" @click="acceptAppointment">
        <button class="m1-bottom"><i class="fas fa-check"></i></button>
        <p class="font-5">ACCEPT</p>
      </div>

      <div class="flex-column decline-button m2-right" @click="declineAppointment">
        <button class="m1-bottom"><i class="fas fa-times"></i></button>
        <p class="font-5">DECLINE</p>
      </div>

      <div class="flex-column decline-button m2-right" @click="cancelAppointment">
        <button class="m1-bottom"><i class="fas fa-times"></i></button>
        <p class="font-5">CANCEL</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Appointment',
  props: ['appointment'],
  computed: {
    date () {
      return new Date(this.appointment.date)
    },
    month () {
      return this.date.toLocaleString('en-us', {
        month: 'long'
      }).toUpperCase()
    },
    day () {
      return this.date.getDay()
    },
    time () {
      return new Date('2020-12-12 ' + this.appointment.time).toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true })
    }
  },
  methods: {
    acceptAppointment () {
    },
    declineAppointment () {
    },
    cancelAppointment () {
    }
  }
}
</script>

